<template>
  <div class="normal">
    <!--  -->
    <div class="select-header">
      <div class="tag-select">
        <div class="tag-name">:</div>
        <a-select
          v-model:value="typeValue"
          @change="changeList"
          style="width: 200px;margin-right:24px"
        >
          <a-select-option v-for="item in typeSource" :key="item.value">{{
            item.label
          }}</a-select-option>
        </a-select>
      </div>

      <a-tooltip title="" color="bule">
        <a-button type="primary" class="but" @click="handleModal('ADD')">
          +  </a-button
        >
      </a-tooltip>
    </div>

    <!--  -->
    <a-table
      :data-source="tableList.arr"
      :columns="columns"
      :rowKey="(record,index)=>{return index}"
      :pagination="pagination"
      :loading="loading"
      @change="handleTableChange"
    >
      <template #operation="{ record }">
        <span @click="handleModal('EDIT', record)">
          <a> </a>
        </span>
        <span style="margin-left: 10px">
          <a-popconfirm
          @confirm="onDelete(record.id)"
          title=""
          ok-text=""
          cancel-text=""
          >
            <a></a>
          </a-popconfirm>
        </span>
      </template>
    </a-table>

    <!--  -->
    <a-modal
      v-model:visible="visible"
      :title="modalTit"
      @ok="handleModal"
      :footer="null"
      width="45%"
      destroyOnClose
    >
      <v-form
        :form="formConfig"
        :formData="formData"
        @onSubmit="handleSubmit"
        :reset="reset"
      />
    </a-modal>
  </div>
</template>

<script lang="ts">
import {
  computed,
  defineComponent,
  ref,
  reactive,
  onMounted,
  toRaw,
} from "vue";
import VForm, { FormItem } from "./formModal/form.vue";
import {
  columnsAll,
  columnsTheme,
  columnsProcess,
  columnsSenario,
  IColumns,
  formAll,
  formTheme,
  formProcess,
  formScenario,
} from "./config";
import { notification } from "ant-design-vue";
import { getFilter } from '@/api/index'
import { getLabelList,addLabel,editLabel,delteLabel } from "@/api/label"
/**
 * 
 */
const typeConfig: Record<
  string,
  { columns: Array<IColumns>; form: Array<FormItem> }
> = {
  TYPE_ALL: {
    columns: columnsAll,
    form: formAll,
  },
  TYPE_THEME: {
    columns: columnsTheme,
    form: formTheme,
  },
  TYPE_BUSIESS_PROCESS: {
    columns: columnsProcess,
    form: formProcess,
  },
  TYPE_BUSIESS_SCENARIO: {
    columns: columnsSenario,
    form: formScenario,
  },
};

type ObjectType = Record<string, unknown>;

export default defineComponent({
  components: {
    VForm,
  },

  setup() {
    const typeValue = ref<string>("TYPE_ALL"); // typeValuevalue
    let modalTit = ref<string>(""); //modalTit
    let loading = ref<boolean>(false); //loading
    const formData = ref<ObjectType>({}); // formData
    const operateType = ref<string>(""); // operateType   
    const listId = ref<unknown>(); // listIdid
    const visible = ref<boolean>(false); // visible
    const reset = ref<boolean>(false);
    const tableList = reactive<{
      arr: ObjectType[];
      pagination: Record<string, unknown>;
    }>({
      arr: [],
      pagination: {},
    });
    const typeSource = [
      {
        label: "",
        value: "TYPE_ALL",
      },
      {
        label: "",
        value: "TYPE_THEME",
      },
      {
        label: "",
        value: "TYPE_BUSIESS_PROCESS",
      },
      {
        label: "",
        value: "TYPE_BUSIESS_SCENARIO",
      },
    ];

    const handleModal = (type: string, record: ObjectType) => {
      operateType.value = type;
      listId.value = toRaw(record) && toRaw(record).id;
      visible.value = !visible.value;
      if (type === "EDIT") {
        modalTit.value = "";
        formData.value = toRaw(record);
        reset.value = false;
      } else if (type === "ADD") {
        formData.value = {};
        reset.value = true;
      }
    };

    /**
     * 
     */
    onMounted(() => {
      feachTableList();
    });

    const feachTableList = (param?: Record<string, unknown>) => {
      loading.value = true;

      getLabelList({
          lable_type: typeValue.value,
          page: 1,
          page_size: 10,
          ...param
      }).then(res=>{
          console.log('res.status_code',res.status_code)
          if (res.status_code === 10000) {
            loading.value = false;
            const { total, from, to, current_page } = res.data;
            tableList.arr = res.data.data;
            tableList.pagination = { total, from, to, current_page };
          }
      })
    };

    /**
     * 
     */
    const changeList = () => {
      feachTableList();
      if (typeValue.value === "TYPE_BUSIESS_PROCESS") {
        getFilter().then(res=>{
          if (res.status_code === 10000) {
            res.data.TYPE_THEME.forEach(
              (item: Record<string, unknown>) => {
                item.value = JSON.stringify(item.value);
              }
            );
            formProcess[1].selectList = res.data.TYPE_THEME;
          }
        })
      }

    };

    /**
     * or
     */
    const handleSubmit = (params: Record<string, unknown>) => {
      params.lable_type = typeValue.value;
      if (operateType.value === "ADD") {
          addLabel(params).then(res=>{
            if (res.status_code === 10000) {
              notification['success']({
                message: '',
              });
              feachTableList();
              setTimeout(() => {
                visible.value = false;
              }, 500);
            }
          })
      } else if (operateType.value === "EDIT") {
        params.id = listId.value;
        editLabel(params).then(res=>{
          if (res.status_code === 10000) {
              notification['success']({
                message: '',
              });
              feachTableList();
              setTimeout(() => {
                visible.value = false;
              }, 500);
            }
        })
      }
    };

    /**
     * 
     */
    const columns = computed(() => {
      return typeConfig[typeValue.value].columns;
    });

    /**
     * 
     */
    const onDelete = (id: string) => {
      delteLabel({id}).then(res=>{
        if (res.status_code === 10000) {
            feachTableList();
            notification['success']({
                message: '',
            });
          }
      })
    };

    /**
     * 
     */
    const pagination = computed(() => ({
      total: toRaw(tableList.pagination).total,
      current: toRaw(tableList.pagination).current_page,
      pageSize: 10,
    }));
    const handleTableChange = (pag: Record<string, number>) => {
      feachTableList({ page: pag.current });
    };

    /**
     * 
     */
    const formConfig = computed(() => {
      return typeConfig[typeValue.value].form;
    });

    return {
      /**  */
      typeValue,
      typeSource,

      /**  */
      columns,
      onDelete,
      tableList,
      loading,
      changeList,
      pagination,
      handleTableChange,

      /**  */
      modalTit,
      visible,
      handleModal,
      reset,

      /**  */
      formConfig,
      formData,
      handleSubmit,
    };
  },
});
</script>
<style lang="less" scoped>
.ant-table-wrapper {
  margin-top: 30px;
}
.normal {
  .select-header {
    position: relative;
    .but {
      position: absolute;
      right: 0;
      top: 0;
    }
  }
}

.tag-select {
  display: flex;
  align-items: center;
  .tag-name {
    width: 80px;
  }
  .ant-select {
    width: 200px;
  }
}
</style>
