<template>
    <div class="row-search">
    <a-row>
      <label>:</label>
      <a-input v-model:value="search"  @change="searchInput" placeholder="" />
    </a-row>
    <a-row>
      <label>:</label>
      <a-select
          showArrow
          style="width: 200px"
          v-model:value="status"
          @change="searchSelect"
          placeholder=""
        >
          <a-select-option v-for="i in stateArr" :value="i.value" :key="i.value">
            {{ i.text }}
          </a-select-option>
        </a-select>
    </a-row>
    <a-button type="primary" @click="onSearch"><SearchOutlined /></a-button>
  </div>
  <a-table
    :rowKey="(record,index)=>{return index}"
    :loading="loading"
    :dataSource="dataSource"
    :columns="columns"
    :pagination="pagination"
    @change="handleChange"
    >
      <template #subject="{ record }">
        <div v-for="sub in subjectArr"  :key="sub.value">
          <span v-if="record.subject == sub.value">{{sub.text}}</span>
        </div>
      </template>
      <template #scene="{ record }">
        <div v-for="sub in sceneArr"  :key="sub.value">
          <span v-if="record.scene == sub.value">{{sub.text}}</span>
        </div>
      </template>
      <template #operation="{ record }">
        <!--  -->
        <span class="table-operation" v-if="record.state === 0 | record.state === 3">
          <a @click="onCheck(record.id)"></a>
          <!--  -->
          <a-popconfirm
            v-if="dataSource.length"
            title=""
            ok-text=""
            cancel-text=""
            @confirm="onServiceState(record.id,-1)"
          >
            <a></a>
          </a-popconfirm>
        </span>
        <!--  -->
        <span class="table-operation" v-if="record.state === 1">
          <a @click="onCheck(record.id)"></a>
          <a @click="onServiceState(record.id,2)"></a>
          <a style="color:'red'" @click="onServiceState(record.id,0)"></a>

          <!--  -->
          <a-popconfirm
            v-if="dataSource.length"
            title=""
            ok-text=""
            cancel-text=""
            @confirm="onServiceState(record.id,-1)"
          >
            <a></a>
          </a-popconfirm>
        </span>
        <!--  -->
        <span class="table-operation" v-if="record.state === 2">
          <a @click="onCheck(record.id)"></a>
          <a @click="onServiceState(record.id,3)"></a>

          <!--  -->
          <a-popconfirm
            v-if="dataSource.length"
            title=""
            ok-text=""
            cancel-text=""
            @confirm="onServiceState(record.id,-1)"
          >
            <a></a>
          </a-popconfirm>
        </span>
      </template>
    </a-table>



</template>

<script lang="ts">
import { defineComponent, computed, toRaw, onMounted, reactive, toRefs } from "vue";
import { SearchOutlined } from "@ant-design/icons-vue";
import { notification } from 'ant-design-vue';
import { Router,useRouter } from "vue-router";
import { getAuditList,serviceState,getFilter} from "@/api/index";
import { columns } from "./tableConfig";


interface IState {
  loading:boolean,
  dataSource:Array<Record<string, unknown>>,
  pageAbout:Record<string, unknown>,
  stateArr:Array<Record<string, unknown>>,
  sceneArr:Array<Record<string, unknown>>,
  subjectArr:Array<Record<string, unknown>>,
}

export default defineComponent({
  name: "Audit",
  components: {
    SearchOutlined,
  },
  setup() {
    const router:Router = useRouter();

    const state:IState = reactive({

      loading:true,
      dataSource:[],
      pageAbout:{},
      sceneArr:[],
      subjectArr:[],

      stateArr:[{
        value:'',
        text:''
      },{
        value:0,
        text:''
      },{
        value:1,
        text:''
      },{
        value:2,
        text:''
      },{
        value:3,
        text:''
      }]
    })

    const params = reactive({
      page:1,
      page_size:10,
      search:'',
      status:'',
    })

    const searchInput = (e:any) =>{
        params.search = e.target.value
    }
    const searchSelect = (value:string) =>{
        params.status = value
    }


    onMounted(() => {
      auditList(params)
    })

    // 
    getFilter().then(res => {
      if (res.status_code == 10000) {
        state.sceneArr = res.data.TYPE_BUSIESS_SCENARIO
        state.subjectArr = res.data.TYPE_THEME
      }
    });
    const auditList = (params: Record<string, unknown>) =>{
        state.loading = true
        getAuditList(params).then(res=>{
          if (res.status_code == 10000) {
            state.loading = false
            state.dataSource = res.data.data
          }
        })
    }
    const onSearch = ()=>{

      auditList(params)
    }

    /**
     * 
     */
    const pagination = computed(() => ({
      total: toRaw(state.pageAbout).total,
      current: toRaw(state.pageAbout).current_page,
      page_size:10,
    }));

    const handleChange = (pag: Record<string, number>) => {
      params.page = pag.current
      auditList(params);
    };

    //
    const onCheck = (id: string) => {
      const disabled = '1'
      router.push({path:'/dataSet/create', query :{ id , disabled }})
    };

    // -1:023:
    const onServiceState = (key: string, status: number) => {
      let successInfo:string = ""
      let errorInfo:string = ""
      switch (status) {
        case -1:
          successInfo = ""
          errorInfo = ""
          break;
        case 0:
          successInfo = ""
          errorInfo = ""
          break;
        case 2:
          successInfo = ""
          errorInfo = ""
          break;
        case 3:
          successInfo = ""
          errorInfo = ""
          break;
      }
      serviceState({id:key,state:status}).then(res=>{
        console.log('status',status)
        if(res.data.code){
          notification['success']({
            message: successInfo,
          });
          auditList(params);
        }else{
           notification['error']({
            message: errorInfo,
          });
        }
      })
    };


    return{
      onSearch,
      columns,

      onCheck,
      onServiceState,
      searchInput,
      searchSelect,

      pagination,
      handleChange,

      ...toRefs(state),
      ...toRefs(params),
    }
  }
})
</script>
<style lang="less" scoped>
.row-search {
  display: flex;
  justify-content: space-between;
  width: 50vw;
  height: 35px;
  line-height: 35px;
  margin: 30px 0px;
}
.row-search label {
  margin-right: 24px;
}
.row-search input,select {
  width: 200px;
}
.table-operation a{
  margin:0 4px;
}
</style>
