import Axios, { AxiosRequestConfig, AxiosResponse, Method } from "axios";
import { message } from "ant-design-vue";
import { useRouter } from "vue-router";

// 
interface PendingType {
  url: string | undefined;
  method: Method | undefined;
  params: Record<string, unknown> | undefined;
  data: Array<string>  | undefined;
  cancel: () => void;
}

// 
const pending: Array<PendingType> = [];
// const CancelToken = Axios.CancelToken;

// axios 
const axios = Axios.create({
  timeout: 6000,
  responseType: "json",
});
const router = useRouter()
// 
const removePending = (config: AxiosRequestConfig) => {
  for (const key in pending) {
    const item: number = +key;
    const list: PendingType = pending[key];
    // 
    if (
      list.url === config.url &&
      list.method === config.method &&
      JSON.stringify(list.params) === JSON.stringify(config.params) &&
      JSON.stringify(list.data) === JSON.stringify(config.data)
    ) {
      // 
      list.cancel();
      // 
      pending.splice(item, 1);
    }
  }
};

// 
axios.interceptors.request.use(
  (config: AxiosRequestConfig) => {
    // const token = Vue.ls.get(ACCESS_TOKEN)
    // if (token) {
    //     config.headers['Authorization'] = 'Bearer ' + token
    // }
    const email = localStorage.getItem("EMAIL")
    if(email){
      config.headers.email = email?.slice(1,-1)
    }
    // token
    // tokentokentoken
    // token
    // if (config.headers.isJwt) {
    // const token = localStorage.getItem("ACCESS_TOKEN");
    // if (token) {
    //   config.headers.Authorization = "Bearer " + token;
    // }
    // }
    return config;
  },
  (error: any) => {
    console.log(error);
  }
);

// 
axios.interceptors.response.use(
  // 
  (response: AxiosResponse) => {
    removePending(response.config);
    return response;
  },
  // 
  (error: any) => {
    const { response } = error;
    if (response) {
      // 2xx
      const response = error.response;

      // code()
      switch (response?.status) {
        case 401:
          // token
          // storage().remove('token');
          // storage('localstorage').remove('token');
          // window.location.href = "/login";
          break;
        case 403:
          // 
          window.location.href = "/403";
          break;
        case 500:
          // 
          // window.location.href = "/503";
          message.error(response.status);
          break;
        case 503:
          // 
          window.location.href = "/503";
          break;
        default:
          break;
      }
      // 
      const config = error.config;
      // ,
      const [RETRY_COUNT, RETRY_DELAY] = [0, 1000];

      if (config && RETRY_COUNT) {
        // 
        config.__retryCount = config.__retryCount || 3;
        // 
        if (config.__retryCount >= RETRY_COUNT) {
          return Promise.reject(response || { message: error.message });
        }
        // 
        config.__retryCount++;
        // Promise
        const backoff = new Promise((resolve: any) => {
          setTimeout(() => {
            resolve();
          }, RETRY_DELAY || 1);
        });
        // instancePromise
        return backoff.then(() => {
          return axios(config);
        });
      }

      // eslint-disable-next-line
        return Promise.reject(response || {message: error.message});
    } else {
      // 
      // eg:statenetwork
      // networkapp.vue
      // 
      
      message.warn(",!");
    }
  }
);

export default axios;
